/*经济运行开始*/
.cont-left {
  width: 30%;
  height: 100%;
  float: left;
}
.cont-right {
  width: 68%;
  height: 100%;
  float: right;
  position: relative;
}
.con-bottom {
  width: 100%;
  height: calc(100% - .4rem);
}
.bo-top {
  width: 100%;
  height: 50%;
}
.bo-top .chart {
  height: calc(100% - 1.2rem);
}
.chartTit-wrap {
  height: .7rem;
  text-align: center;
  background: url(../images/v2.0/heng-line.png) no-repeat center bottom;
  background-size: 100%;
}
.bo-bottom {
  width: 100%;
  height: 50%;
  padding-top: .1rem;
}
.bo-bottom .chart {
  position: relative;
  height: calc(100% - .5rem);
}
.cr-top {
  width: 100%;
  height: 67%;
}
.ctLeft {
  width: 55%;
  height: 100%;
  float: left;
}
.index-wrap {
  height: 1rem;
}
.economyBox {
  height: calc(100% - 2rem);
}
.economyBox .con-tit {
  height: .4rem;
}
.economyBox .co-box {
  height: calc(100% - .4rem);
}
.economyBox .leftChart {
  position: relative;
  width: 59%;
  height: 100%;
  float: left;
  background: url("../images/v2.0/su-line.png") no-repeat right bottom;
}
.economyBox .rightChart {
  width: 39%;
  height: 100%;
  float: right;
}
.ctRight {
  width: 43%;
  height: 100%;
  float: right;
}
.ctRight .co-box {
  height: calc(100% - .4rem);
}
.ctRight .estateTop {
  width: 100%;
  height: 44%;
  background: url("../images/v2.0/heng-line.png") no-repeat center bottom;
}
.ctRight .estateTop .pieLeft,
.ctRight .estateTop .pieRight {
  float: left;
  width: 50%;
  height: 100%;
}
.ctRight .estateTop .pieLeft .chart-tit,
.ctRight .estateTop .pieRight .chart-tit {
  font-size: .16rem;
  text-align: center;
}
.ctRight .estateTop .pieLeft .chart,
.ctRight .estateTop .pieRight .chart {
  width: 100%;
  height: calc(100% - .5rem);
}
.ctRight .estateBottom {
  width: 100%;
  height: 56%;
}
.cr-bottom {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 29.5%;
}
.cr-bottom .con-tit {
  padding: .1rem 0 0 .12rem;
}
.cr-bottom .three-Chart {
  width: 100%;
  height: calc(100% - .4rem);
}
.cr-bottom .three-Chart .left {
  float: left;
  width: 23%;
  height: 100%;
  position: relative;
  background: url("../images/v2.0/su-line.png") no-repeat right center;
}
.cr-bottom .three-Chart .left .peopleNum {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
}
.cr-bottom .three-Chart .left .peopleNum p {
  text-align: center;
  color: #0cbcaf;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 5px;
}
.cr-bottom .three-Chart .left .peopleNum p i {
  margin-right: 4px;
}
.cr-bottom .three-Chart .left .peopleNum p.huji {
  color: #0077d8;
}
.cr-bottom .three-Chart .middle {
  float: left;
  width: 33%;
  height: 100%;
  background: url("../images/v2.0/su-line.png") no-repeat right center;
}
.cr-bottom .three-Chart .right {
  position: relative;
  float: left;
  width: 44%;
  height: 100%;
}
/*经济运行结束*/
